body {
  font-family: "Press Start 2P";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Press Start 2P';
  font-style: normal;
  font-weight: 400;
  src: url('./font/PressStart2P-Regular.ttf'); 
  background: grey;
}

.copyButton {
  font-size: 1em;
  position: absolute;
  bottom: 2px;
  right: 2px;
  font-family: "Press Start 2P";
}

.messageBlock {
    padding: 1em;
    position: absolute;
    border: solid 32px grey;
    bottom: 10px;
    top: 10px;
    left: 10px;
    right: 10px;
    background: blue;
    color: white;
    overflow: auto;
}

.topBar {
  padding: 10px;
  position: absolute;
  right: 0;
  color: white;
  z-index: 1;
}